.node {
    position: relative;
    height: 40px;
    width: 40px;
    background-color: #b2ebf2;
    border-radius: 100%;
}

.node .react-flow__handle {
    visibility: hidden;
}

.node.visited {
    background-color: #a5d6a7;
}

.node .label {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.node .shortest-path {
    position: absolute;
    bottom: -20px;
    left: 20px;
    font-size: 10px;
    font-weight: bold;
    color: #a5d6a7;
}
