.root {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.appbar {
  background-color: #2d3748 !important;
}

.grid-container {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.content-container {
  padding: 1rem;
  height: 100%;
  background-color: #edf2f7;
}

.content-container .title {
  font-size: 1.25rem;
  font-weight: 600;
}

.content-container .color-block {
  border-radius: 100%;
  display: inline-block;
  min-height: 30px;
  min-width: 30px;
}

.content-container .color-block-container {
  margin-bottom: 10px;
}

.content-container .color-block.blue {
  background-color: #b2ebf2;
}

.content-container .color-block.green {
  background-color: #a5d6a7;
}

.content-container .color-block.grey {
  background-color: #b1b1b7;
}

.content-container .color-block.yellow {
  background-color: #FFD54F;
}


.graph-container {
  width: 100%;
  min-height: 100%;
}

#notistack-snackbar {
  padding: 12px 14px;
  line-height: 25px;
  vertical-align: middle;
  font-size: 18px;
}

#notistack-snackbar .MuiSvgIcon-root {
  width: 25px;
  height: 25px;
  padding-right: 5px;
}
